.skills {}

@media print {
    #skills{display:none;}
}


.skills>.wrapper {
    display: flex;
    justify-content: space-between;
}

.skills>.wrapper>:first-child {
    margin-right: 24px;
}

.skills>.wrapper>#skills {}

.skills>.wrapper>ul {
    color: #666;
}

.skills>.wrapper strong {
    color: #05aea6;
    font-weight: normal;
}

.skills>.wrapper>ul>li {
    line-height: 1.2;
    margin-bottom: 8px;
    list-style: disc inside;
}